.contact {
  padding: 10vw 20vw;
  text-align: center;
  margin-bottom: -10vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: url('./footer_bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.contact__title {
  margin-bottom: 6vh;
  margin-top: 3vh;
  font-size: 4vw;
  color: black;
  font-weight: 400;
}

.contact__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2vw;
}

.contact__content > a{
  font-size: 1.7vw;
  text-decoration: none;
  color: #25262A;
  line-height: 1.2;
  font-weight: 500;
  margin-bottom: 1vw;
  transition: opacity 0.5s;

  &:hover {

    opacity: 0.6;
  }

  // hr {
  //   border: none;
  //   border-bottom: 2px solid $pink;
  //   width: 0;
  //   transition: width 0.6s;
  //   margin: 0;
  // }
  
  // &:hover {
  //   color: $pink;
  //   hr {
  //     width: 100%;
  //   }
  // }
}

.contact__information {
  font-size: 1.2vw;

  a {
    text-decoration: none;
    color: inherit;
    font-size: inherit;
  }
}

@media (max-width: $desktop-breakpoint) {
  .contact {
    margin-bottom: 0;
    padding: 10vh 4vw;
  }
  .contact__title {
    margin-bottom: 5vh;
    font-size: 8vw;
  }
  .contact__content > a{
    font-size: 5vw;
    margin-bottom: 1.5vw;
  }
  .contact__content {
    margin-bottom: 4vw;
  }
}