.about {
  padding: 10vw 8vw;
  text-align: left;
  background-color: white;
  color: black;
}

.about__title {
  font-size: 5em;
  margin: 0;
}

.about__greet {
  display: flex;
  align-items: flex-end;
  margin-bottom: 8vw;
}

.about__greet > hr {
  width: 50px;
  border: 5px solid $pink;
  border-top: none;
  border-right: none;
  border-left: none;
  margin: 0 20px 0 10px;
  margin-bottom: 1em;
}

.about__content {
  font-size: 3em;
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
  margin-left: 10vw;
}

@media (max-width: $desktop-breakpoint) {
  .about {
    padding: 10vh 8vw;
  }
  .about__title {
    font-size: 12vw;
  }
  .about__content {
    font-size: 7vw;
  }
  .about__content-main {
    font-size: 9vw;
  }
}