.projects {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}

.projects__right {
  margin-top: 100px;
}

.project__categories {
  color: $pink;
  margin: 0;
  max-width: 60vw;
}

.project__categories > a {
  color: white;
  text-transform: uppercase;
  transition: all 0.5s;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 2px;

  &:hover {
    color: $pink;
  }
}

.project__year {
  color: white;
  position: absolute;
  top: 30vh;
  right: -1vw;
  margin: 0;
  transform: rotate(270deg);
  font-size: 1.2em;
  font-weight: 600;
}

.project__explore {
  display: flex;
  align-items: center;
  position: absolute;
  right: -4vw;
  bottom: 10vh;

  &::before {
    content: "";
    position: absolute;
    left: -62px;
    width: 60px;
    height: 2px;
    top: 50%;
    background: white;
    transition: all 0.5s;
    border: none;
  }
  &:hover::before {
    background: $pink;
  }
}

.project__explore-text > p {
  color: white;
  padding: 15px;
  text-decoration: none;
  font-weight: 500;
  margin: 0;
  transition: color 0.5s;
  
  &:hover {
    color: $pink
  }
}

.project__explore-text {
  
  &::before {
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    width: 0;
    height: 0;
    background: transparent;
    border: 2px solid transparent;
    transition: all 0.5s;
    animation: out-animate 0.6s linear backwards;
  }
  &:hover::before {
    animation: animate 0.6s linear forwards;
  }
  
  &::after {
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    width: 0;
    height: 0;
    background: transparent;
    border: 2px solid transparent;
    animation: out-animates 0.6s linear backwards;
  }
  &:hover::after {
    animation: animates 0.6s linear forwards;
  }
}

.projects__project {
  margin-bottom: 10vh;
  position: relative;
}

.project__overlay {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  top: 10vh;
  left: -3vw;
}

.project__overlay > h1 {
  color: white;
  font-family: 'Poppins', 'Sans-serif';
  font-weight: 400;
  display: inline-block;
  font-size: 4vw;
  margin-bottom: 10px;
  border-radius: 5px;
}

.projects__image {
  height: 50vh;
  width: 40vw;
  object-fit: fill;
  margin: 5vh 0;
  mask-image: linear-gradient(rgba(0,0,0,.4), rgba(0,0,0,.4));

  
}

.project__bottom {
  margin-bottom: '10vh';
}

@media (max-width: $desktop-breakpoint) {
  .projects {
    flex-direction: column;
    justify-items: center;
  }
  .projects__left {
    margin: 0 auto;
    margin-bottom: -4vh;
  }
  .projects__right {
    margin: 0 auto;
  }
  .projects__image {
    width: 80vw;
    height: auto;
    margin: 4vh auto;
    object-fit: contain;
  }
  .projects__project {
    margin: 3vh auto;
    width: max-content;
  }
  .project__overlay {
    position: absolute;
    top: 6vh;
    left: -5vw;
    margin: 0;
  }
  .project__overlay > h1 {
    font-size: 10vw;
  }
  .project__overlay > div {
    padding: 10px 25px;
  }
  .project__button {
    padding: 8px 20px;
  }
  .project__year {
    top: 15vh;
    right: -3vw;
  }
  .project__bottom {
    margin-bottom: 5vh;
  }
  .project__explore {
    right: -8vw;
    bottom: 7vh;
  }
}

@keyframes animate {
  0% {
      width: 0;
      height: 0;
      border-top-color: $pink;
      border-right-color: transparent;
      border-bottom-color: transparent;
      border-left-color: transparent;
  }

  50% {
      width: 100%;
      height: 0;
      border-top-color: $pink;
      border-right-color: $pink;
      border-bottom-color: transparent;
      border-left-color: transparent;
  }

  100% {
      width: 100%;
      height: 100%;
      border-top-color: $pink;
      border-right-color: $pink;
      border-bottom-color: transparent;
      border-left-color: transparent;
  }
}

@keyframes animates {
  0% {
      width: 0;
      height: 0;
      border-top-color: transparent;
      border-right-color: transparent;
      border-bottom-color: transparent;
      border-left-color: $pink;
  }

  50% {
      width: 0;
      height: 100%;
      border-top-color: transparent;
      border-right-color: transparent;
      border-bottom-color: $pink;
      border-left-color: $pink;
  }

  100% {
      width: 100%;
      height: 100%;
      border-top-color: transparent;
      border-right-color: transparent;
      border-bottom-color: $pink;
      border-left-color: $pink;
  }
}

@keyframes out-animate {
  0% {
    width: 100%;
    height: 100%;
    border-top-color: $pink;
    border-right-color: $pink;
    border-bottom-color: transparent;
    border-left-color: transparent;
  }

  50% {
    width: 100%;
    height: 0;
    border-top-color: $pink;
    border-right-color: $pink;
    border-bottom-color: transparent;
    border-left-color: transparent;
  }

  100% {
    width: 0;
    height: 0;
    border-top-color: $pink;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-left-color: transparent;
  }
}

@keyframes out-animates {
  0% {
    width: 100%;
    height: 100%;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: $pink;
    border-left-color: $pink;
  }

  50% {
    width: 0;
    height: 100%;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: $pink;
    border-left-color: $pink;
  }

  100% {
    width: 0;
    height: 0;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-left-color: $pink;
  }
}