.home {
  // font-family: 'Rubik', sans-serif;
  font-family: 'Arimo', sans-serif;
  display: flex;
  justify-content: space-evenly;
  padding: 6vw 4vw 14vw 4vw;
  text-align: left;
}

.home__greet {
  display: flex;
  align-items: center;
  margin-bottom: 3vh;
  
  h2 {
    margin: 0;
    font-size: 2.5vw;
  }
}

.home__greet > hr {
  width: 50px;
  border: 5px solid white;
  border-top: none;
  border-right: none;
  border-left: none;
  margin: 0 20px 0 10px;
  float: left;
}

.home__image {
  height: 65vh;
  width: 30vw;
  object-fit: contain;
}

.home__title {
  font-size: 5vw;
  margin-top: 15px;
  margin-bottom: 15px;
}

.home__tagline {
  font-size: 2vw;
  line-height: 1.5;
}

.home__title > span {
  font-weight: 400;
}

.home__content {
  display: flex;
  flex-direction: column;
  height: min-content;
  margin: auto 0;
  line-height: 1;
}

@media (max-width: $desktop-breakpoint) {
  .home {
    flex-direction: column;
    padding-top: 7vh; 
    padding-bottom: 7vh;
  }

  .home__content {
    margin: 0 auto;
    width: 80vw;
  }
  
  .home__title {
    font-size: 13vw;
  }

  .home__tagline {
    font-size: 5vw;
  }

  .home__greet{
    h2 {
      font-size: 5vw;
    }

    hr {
      width: 30px;
    }
  }

  .home__image {
    width: 80vw;
    object-fit: contain;
    margin: 5vh auto;
  }
}