.work {
  font-family: 'Rajdhani', sans-serif;
  padding: 10vw 5vw;
}

.work__greet {
  display: flex;
  align-items: flex-end;
  margin-bottom: 6vw;
}

.work__greet > hr {
  width: 50px;
  border: 5px solid $pink;
  border-top: none;
  border-right: none;
  border-left: none;
  margin: 0 20px 0 10px;
  margin-bottom: 1em;
}

.work__title {
  font-size: 6vw;
  // font-family: 'Rubik', sans-serif;
  font-family: 'Arimo', sans-serif;
  text-align: left;
  font-weight: 500;
  letter-spacing: 2px;
  margin: 0;
}

.work__title > span{
  -webkit-text-stroke: 1px white;
  -webkit-text-fill-color: transparent;
}

@media (max-width: $desktop-breakpoint) {
  .work {
    padding: 10vh 5vw;
    padding-bottom: 8vh;
  }
  .work__title {
    font-size: 10vw;
  }
}