.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  text-align: left;
  background-color: #25262A;
  opacity: 0.95;
  position: sticky;
  top: 0;
  z-index: 1;
}

.navbar__menu {
  font-size: small;
  border-radius: 100%;
  padding: 10px;
  background-color: black;
  color: white;
  height: 30px;
  width: 30px;
  margin-right: 15px;

  &:hover {
    cursor: pointer;
  }

  @media (min-width: $desktop-breakpoint) {
    display: none !important;
  }
}

.navbar__brand {
  font-size: 2.2em;
  margin: 0;
  margin-left: 20px;
}

.navbar__links {
  display: flex;
  justify-content: flex-end;
  margin-right: 20px;
}

.navbar__close {
  color: white;
  position: absolute;
  top: 10px;
  right: 10px;
  height: 20px;
  width: 20px;

  &:hover {
    cursor: pointer;
  }

  @media (min-width: $desktop-breakpoint) {
    display: none !important;
  }
}

.navbar__link-main {
  text-decoration: none;
  color: white;
  padding: 2.2vh 0 2.2vh 5vw;
  font-size: 1.1em;
  font-weight: 800;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;

  hr {
    border: none;
    border-bottom: 2px solid white;
    width: 0;
    transition: width 0.6s;
    margin: 0;
  }
  
  &:hover {
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;

    hr {
      width: 100%;
    }
  }
}

@media (max-width: $desktop-breakpoint) {
  .navbar {
    justify-content: space-between;
    top: 0px;
    opacity: 1;
  }
  .navbar__brand {
    margin-left: 10px;
  }
  .navbar__links {
    z-index: 1000;
    margin-left: 0;
    padding-top: 20px;
    justify-content: start;
    width: 70vw;
    transition: 0.5s;
    position: fixed;
    background-color: black;
    height: 100%;
    top: 0;
    left: -70vw;
    flex-direction: column;
  }
  .navbar__link-main {
    font-size: 10vw;
    transition: opacity 1s;
    -webkit-text-stroke: 2px white;
    -webkit-text-fill-color: transparent;

    &:hover {
      -webkit-text-stroke-color: $pink;
      hr {
        border-color: $pink;
        width: 80%;
      }
    }
  }
}