.social {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.social__icon {
  height: 33px;
  width: 33px;
  margin: 0 2vw;
  text-decoration: none;
  padding: 0; 
  font-size: 33px;
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.5;
  }
}

@media (max-width: $desktop-breakpoint) {
  .social__icon {
    margin: 0 5vw;
    width: 28px;
    height: 28px;
  }
}